import React from 'react';
import { Layout, Menu, Typography } from 'antd';
import { HomeOutlined, AppstoreOutlined, BulbOutlined, UserOutlined, PhoneOutlined } from '@ant-design/icons';

const { Header } = Layout;
const { Text } = Typography;

const CustomHeader = () => {
  return (
    <Header>
      <div className="logo" />
      <Menu theme="dark" mode="horizontal" defaultSelectedKeys={['home']} style={{ lineHeight: '64px' }}>
        <Menu.Item key="home" icon={<HomeOutlined />}>
          MGTECH
        </Menu.Item>
        <Menu.Item key="services" icon={<AppstoreOutlined />}>
          Servios
        </Menu.Item>
        <Menu.Item key="about" icon={<BulbOutlined />}>
          Sobre
        </Menu.Item>
        <Menu.Item key="contact" icon={<PhoneOutlined />}>
          Contato
        </Menu.Item>
        <Menu.Item key="login" icon={<UserOutlined />} style={{ marginLeft: 'auto', float: 'right' }}>
          Login
        </Menu.Item>
      </Menu>
    </Header>
  );
};

export default CustomHeader;
