import React from 'react';
import { Layout, Typography, Row, Col, Form, Input, Button } from 'antd';

const { Content } = Layout;
const { Title } = Typography;

const Contact = () => {
  const onFinish = (values) => {
    console.log('Valores do formulrio:', values); // Esta ao pode ser modificada para enviar os dados para seu servidor
  };

  return (
    <Content style={{ padding: '50px', background: '#f0f2f5' }}>
      <Title level={2} style={{ textAlign: 'center', marginBottom: '40px' }}>Entre em Contato</Title>
      <Row justify="center">
        <Col xs={24} sm={20} md={12} lg={10}>
          <Form
            name="contactForm"
            onFinish={onFinish}
            layout="vertical"
            initialValues={{ remember: true }}
          >
            <Form.Item
              label="Nome"
              name="name"
              rules={[{ required: true, message: 'Por favor, insira seu nome!' }]}
            >
              <Input />
            </Form.Item>
            <Form.Item
              label="E-mail"
              name="email"
              rules={[
                { required: true, message: 'Por favor, insira seu e-mail!' },
                { type: 'email', message: 'Por favor, insira um e-mail vlido!' },
              ]}
            >
              <Input />
            </Form.Item>
            <Form.Item
              label="Mensagem"
              name="message"
              rules={[{ required: true, message: 'Por favor, insira sua mensagem!' }]}
            >
              <Input.TextArea rows={4} />
            </Form.Item>
            <Form.Item>
              <Button type="primary" htmlType="submit">
                Enviar Mensagem
              </Button>
            </Form.Item>
          </Form>
        </Col>
      </Row>
    </Content>
  );
};

export default Contact;
