import React from 'react';
import { Carousel } from 'antd';
import './banner.css'; // Criaremos isso depois
import sobreImage from '../../assets/6018990.jpg';
const Banner = () => {
    const imageUrl = 'https://images.unsplash.com/photo-1454165804606-c3d57bc86b40?q=80&w=2070&auto=format&fit=crop&ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8fA%3D%3D';
  
    return (
      <div className="banner">
        <Carousel autoplay>
          <div>
            <img src={sobreImage} alt="Banner" style={{ width: '100%', height: '100%' }} />
          </div>
          {/* Adicione mais imagens ou contedo do banner aqui */}
        </Carousel>
      </div>
    );
  };
  
  export default Banner;
