import React from 'react';
import { Layout, Typography, Row, Col, Divider } from 'antd';

const { Footer } = Layout;
const { Title, Text, Link } = Typography;

const CustomFooter = () => {
  return (
    <Footer style={{ backgroundColor: '#001529', padding: '50px 0', color: '#fff' }}>
      <Row justify="space-around" align="top">
        <Col xs={24} sm={12} md={6}>
          <Title level={4} style={{ color: '#fff' }}>Sobre Ns</Title>
          <Text style={{ color: '#fff' }}>
            Somos uma empresa de desenvolvimento de software comprometida em oferecer solues digitais de
            qualidade para nossos clientes.
          </Text>
        </Col>
        <Col xs={24} sm={12} md={6}>
          <Title level={4} style={{ color: '#fff' }}>Links Rpidos</Title>
          <ul style={{ listStyle: 'none', padding: 0 }}>
            <li>
              <Link href="#home" style={{ color: '#fff' }}>Home</Link>
            </li>
            {/* Adicione o estilo de cor branca para outros links tambm */}
          </ul>
        </Col>
        <Col xs={24} sm={12} md={6}>
          <Title level={4} style={{ color: '#fff' }}>Contato</Title>
          <Text style={{ color: '#fff' }}>E-mail: contato@exemplo.com</Text>
          <br />
          <Text style={{ color: '#fff' }}>Telefone: +55 11 1234-5678</Text>
        </Col>
      </Row>
      <Divider style={{ margin: '30px 0', borderColor: '#fff' }} />
      <Text style={{ textAlign: 'center', display: 'block', color: '#fff' }}>
         {new Date().getFullYear()} Sua Empresa. Todos os direitos reservados.
      </Text>
    </Footer>
  );
};

export default CustomFooter;
