import React from 'react';
import { Layout, Typography, Row, Col, Card } from 'antd';

const { Content } = Layout;
const { Title } = Typography;

const Services = () => {
  const servicesData = [
    {
      title: 'Desenvolvimento Web',
      description: 'Criamos websites personalizados para atender s suas necessidades.',
      icon: 'https://images.unsplash.com/photo-1487014679447-9f8336841d58?q=80&w=1905&auto=format&fit=crop&ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8fA%3D%3D', // Adicione o URL do cone para este servio
    },
    {
      title: 'Aplicativos Mveis',
      description: 'Desenvolvemos aplicativos para iOS e Android.',
      icon: 'https://images.unsplash.com/photo-1601972602237-8c79241e468b?q=80&w=2070&auto=format&fit=crop&ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8fA%3D%3D', // Adicione o URL do cone para este servio
    },
    {
      title: 'Consultoria em TI',
      description: 'Oferecemos consultoria especializada para otimizar sua infraestrutura de TI.',
      icon: 'https://images.unsplash.com/photo-1516321318423-f06f85e504b3?q=80&w=2070&auto=format&fit=crop&ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8fA%3D%3D', // Adicione o URL do cone para este servio
    },
  ];

  return (
    <Content style={{ padding: '50px', background: '#fff' }}>
      <Title level={2} style={{ textAlign: 'center', marginBottom: '40px' }}>Nossos Servios</Title>
      <Row gutter={[16, 16]} justify="center">
        {servicesData.map((service, index) => (
          <Col key={index} xs={24} sm={12} md={8}>
            <Card
              hoverable
              cover={<img alt={service.title} src={service.icon} style={{ height: '150px', objectFit: 'cover' }} />}
            >
              <Card.Meta title={service.title} description={service.description} />
            </Card>
          </Col>
        ))}
      </Row>
    </Content>
  );
};

export default Services;
